pre {
	position: relative;
    overflow: auto;
    margin: 30px 0 40px 0;
    color: #c5c8c6;
    background-color: #1d1f21;
    border-radius: 2px;
    box-shadow: 0 20px 25px -20px rgba(0,0,0,0.7);
    text-align: justify !important
}

#bodytext div{
    line-height: 1.5; 
    text-align: center !important
}

@media only screen and (max-width: 800px) {
    
    iframe {
        width: 100%;
        height: 400px;
    }

    img {
        width: 100%;
        height: auto;
    }
}
